/*

===========================================================================
Base: Utilities
===========================================================================

Version:            3.0.2

//                  3.0.2 Clearfix flexbox properties

*/
.cf {
  *zoom: 1;
}
.cf:before,
.cf:after {
  content: "";
  display: table;
  flex-basis: 0;
  order: 1;
}
.cf:after {
  clear: both;
}
.hidden-visually,
.hide-v {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.hidden-visually.focusable:active,
.hide-v.focusable:active,
.hidden-visually.focusable:focus,
.hide-v.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}
.hide-text {
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  display: block;
}
.unhide-text {
  text-indent: 0;
  white-space: normal;
}
.hidden-abs {
  position: absolute;
  left: -10000px;
  top: -10000px;
}
.hidden-abs-no {
  position: static;
  left: auto;
  top: auto;
}
.no-select {
  user-select: none;
  pointer-events: none;
  cursor: pointer;
}
.no-scroll-y {
  overflow-y: hidden;
}
.no-scroll-x {
  overflow-x: hidden;
}
